<template>
  <div>
    <v-card>
      <v-form ref="form">
        <v-card-text>
          <v-container v-if="status === 'signup'">
            <v-row justify="center" align="center">
              <v-col cols="12" sm="6" md="4">
                <v-text-field
                  label="*  "
                  required
                  v-model="nickname"
                  :rules="[
                    () => !!nickname || '   .',
                    () =>
                      (!!nickname && nickname.length >= 2 && nickname.length <= 30) ||
                      ' 2   30    .',
                  ]"
                ></v-text-field>

                <v-text-field
                  :rules="[emailRules.required, emailRules.email]"
                  color="red"
                  label="* "
                  required
                  v-model="email"
                ></v-text-field>

                <v-text-field
                  :rules="[passwordRules.required, passwordRules.counter]"
                  color="red"
                  label="* "
                  required
                  v-model="password"
                ></v-text-field>

                <v-text-field
                  :rules="[passwordRules.required, passwordRules.counter, passwordConfirmationRule]"
                  color="red"
                  label="*  "
                  required
                  v-model="rePassword"
                ></v-text-field>

                <v-radio-group v-model="gender" row>
                  <v-radio label="" value="MALE"></v-radio>
                  <v-radio label="" value="FEMALE"></v-radio>
                </v-radio-group>
                <v-radio-group v-model="birthdayType" row>
                  <v-radio label="" value="SOLAR"></v-radio>
                  <v-radio label="" value="LUNAR"></v-radio>
                </v-radio-group>

                <v-text-field
                  label="*  (: 19950318) "
                  required
                  v-model="birthday"
                  :rules="[
                    () => !!birthday || '  .',
                    () => (!!birthday && birthday.length == 8) || 'Birthday must be 8 characters',
                    (v) =>
                      /^(19[0-9][0-9]|20\d{2}|2100)(0[0-9]|1[0-2])(0[1-9]|[1-2][0-9]|3[0-1])$/g.test(v) ||
                      'Input 1900 ~ 2100 year',
                  ]"
                  type="number"
                ></v-text-field>
                <v-text-field
                  label=" (: 0724)  "
                  v-model="time"
                  :rules="[
                    () => !time || (!!time && time.length == 4) || 'Time must be 0 or 4 characters',
                    (v) =>
                      !time ||
                      /(0[0-9]|1[0-9]|2[0-3])(0[0-9]|[1-5][0-9])$/g.test(v) ||
                      'Input 00 ~ 23 hours, 00 ~ 59 minutes',
                  ]"
                  type="number"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row justify="center" align="center">
              <v-btn outlined @click="onClickSignUp"></v-btn>
            </v-row>
            <br />
          </v-container>
          <v-container v-else>
            <v-row justify="center" align="center">
              <v-col cols="12" sm="6" md="4">
                <v-text-field
                  :rules="[emailRules.required, emailRules.email]"
                  color="red"
                  label="* "
                  required
                  v-model="email"
                ></v-text-field>

                <v-text-field
                  :rules="[passwordRules.required, passwordRules.counter]"
                  color="red"
                  label="* "
                  required
                  v-model="password"
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row justify="center" align="center">
              <v-btn outlined @click="onClickSignIn"></v-btn>
            </v-row>
            <br />
          </v-container>

          <v-radio-group v-model="status" row>
            <v-radio label="" value="signin"></v-radio>
            <v-radio label="" value="signup"></v-radio>
          </v-radio-group>
        </v-card-text>
      </v-form>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "SignUpForm",
  data() {
    return {
      nickname: "",
      email: "",
      password: "",
      rePassword: "",
      status: "signin",
      birthday: "",
      birthdayType: "SOLAR",
      gender: "MALE",
      time: "",
      passwordRules: {
        required: (value) => !!value || "  .",
        counter: (value) =>
          (value.length >= 4 && value.length <= 100) || " 4  100   .",
      },
      emailRules: {
        required: (value) => !!value || "  .",
        counter: (value) => value.length <= 100 || "Max 100 characters",
        email: (value) => {
          const pattern =
            /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "  .";
        },
      },
    };
  },
  computed: {
    passwordConfirmationRule() {
      return () => this.password === this.rePassword || "  .";
    },
  },
  methods: {
    onClickSignUp() {
      if (this.$refs.form.validate() == false) {
        return;
      }

      this.$emit("signup", this);
    },
    onClickSignIn() {
      if (this.$refs.form.validate() == false) {
        return;
      }

      this.$emit("signin", this);
    },
  },
};
</script>
